/* 页面背景 */
body {
  background: #f4f7fc;
  font-family: "Arial", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

/* 外部容器 */
.app-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

/* 聊天窗口卡片 */
.chat-card {
  background: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  padding: 20px;
  width: 400px;
  text-align: center;
}

/* 标题样式 */
h1 {
  font-size: 22px;
  color: #333;
  margin-bottom: 15px;
}
